:root {
  --font-primary: "Manrope", sans-serif;
}

:root {
  --lue: hsl(217, 19%, 35%);
  --lue: hsl(214, 17%, 51%);
  --lue: hsl(212, 23%, 69%);
  --g: hsl(210, 46%, 95%);
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  width: 90vw;
  max-width: 68.75rem;
}